body {
  background-image: url(/weather_app/static/media/bright-squares.53c1ec5f.png);
  background-color: #000f36;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main_header {
    font-family: 'Open Sans', sans-serif;
    padding-top: 20px;
    color: #000f36;
}
.searchWrap {
    font-family: 'Open Sans', sans-serif;
    display: block;
}

.searchTerm {
    border: 1.75px solid #000f36;
}

.searchButton {
    font-family: 'Open Sans', sans-serif;
    height: 20px;
    width: 25px;
    background-color: #000f36;
    border-color: #000f36;
}
.recentSearches {
    color: #000f36;
    font-family: 'Open Sans', sans-serif;
}

.searchButtons {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
}

.rsb {
    font-family: 'Open Sans', sans-serif;
    min-height: 30px;
    min-width: 40px;
    margin: 2px;
    background-color: #000f36;
    border-color: #36405b;
    border-radius: 3px;
    color: white;
    font-size: 20px;
}
.weatherCard {
    font-family: 'Lato', sans-serif;
    margin: 2px;
    padding: 10px;
    background-color: #000f36;
    border-radius: 5px;
    color: white;
    min-width: 20vw;
    min-height: 50vh;
    flex-flow: column;
}

.mainTemp {
    font-size: 90px;
}


p {
    font-size: 20px;
}
.searchResults {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.App {
  background-color: rgb(220, 225, 241);
  min-height: 60vh;
  border-radius: 5px;
  text-align: center;
}



